import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Project } from '../shared/project.model';
import { Sprint } from '../shared/sprint.model';
import { SprintService } from '../shared/sprint.service';
import { ProjectService } from '../shared/project.service';
import { ToastController, IonContent } from '@ionic/angular';

@Component({
  selector: 'app-sprintform',
  templateUrl: './sprintform.page.html',
  styleUrls: ['./sprintform.page.scss'],
})
export class SprintformPage implements OnInit {

  constructor(public sprintService: SprintService,
    public projectService: ProjectService,
    public toastController: ToastController) { }

  ngOnInit() {
  }

  async presentToast(text, theme) {
    const toast = await this.toastController.create({
      message: text,
      showCloseButton: true,
      position: 'top',
      closeButtonText: 'Done',
      color: theme
    });
    toast.present();
  }

  showForEdit(spr: Sprint) {
    this.sprintService.selectedSprint = Object.assign({}, spr);
  }

  onDelete(id: number) {
    if (confirm('Are you sure to delete this record ?') == true) {
      this.sprintService.deleteSprint(id)
        .subscribe(x => {
          this.sprintService.getAllSprints();
          this.presentToast('Sprint deleted!', 'danger');
        })
    }
  }

  resetForm(form?: NgForm) {
    if (form != null) {
      form.reset();
    }
    this.sprintService.selectedSprint = {
      id: null,
      name: '',
      description: '',
      startDate: '',
      endDate: '',
      percent: 0,
      priority: 0,
      isDone: false,
      projects: []
    }

  }

  onSubmit(form: NgForm) {
    if (form.value.ID == null) {
      this.sprintService.postSprint(form.value).subscribe(data => {
        this.resetForm(form);
        this.sprintService.getAllSprints();
        this.projectService.getAllProjects();
        this.presentToast('Sprint created!', 'success');
      })
    }
    else {
      this.sprintService.putSprint(form.value.ID, form.value).subscribe(data => {
        this.resetForm(form);
        this.sprintService.getAllSprints();
        this.projectService.getAllProjects();
        this.presentToast('Sprint updated!', 'success');
      })
    }
  }

  compareFn(e1: Project, e2: Project): boolean {
    return e1 && e2 ? e1.id === e2.id : e1 === e2;
  }

  containsObject(obj: Project, list: Project[]) {
    //console.log(`enter values ${obj} - ${list}`);
    let project = Object.assign({}, obj);
    let list1 = Object.assign({}, list);
    let i;
    for (i = 0; i < list1.length; i++) {
      if (list[i] === project) {
        console.log(true);
        return "true";
      }
    }

    return "false";
  }

}
